import { Component } from 'react'
import { Input, Tooltip } from 'antd'
import { CopyToClipboard } from 'react-copy-to-clipboard'

import randomNationId from '../../../libraries/randomNationId'

const { Search } = Input

class Da extends Component {
  constructor(props) {
    super(props)
    this.state = {
      value: '',
      array: [],
      loading: false,
    }
    this.handleOnChange = this.handleOnChange.bind(this)
    this.handleOnSearch = this.handleOnSearch.bind(this)
  }

  handleOnSearch(value) {
    const { id } = randomNationId(parseInt(value))
    this.setState({ array: id })
    console.log(this.state.array)
  }

  handleOnChange(e) {
    const { value } = e.target
    this.setState({ value: value })
  }

  render() {
    return (
      <div>
        <Tooltip
          trigger={['focus']}
          title="คัดลอกแล้ว !!"
          placement="topLeft"
          overlayClassName="numeric-input"
        >
          <CopyToClipboard text={this.state.value}>
            <Input
              placeholder="กดปุ่มเพื่อสุ่มเลขบัตร"
              // enterButton="Search"
              readOnly
              value={this.state.value}
              size="large"
            />
          </CopyToClipboard>
        </Tooltip>
        <Search
          placeholder="กดปุ่มเพื่อสุม"
          enterButton="Search"
          value={this.state.value}
          size="large"
          onSearch={this.handleOnSearch}
          onChange={this.handleOnChange}
        />
        {this.state.array.map(item => (
          <Input value={item} key={item} readOnly />
        ))}
      </div>
    )
  }
}
export default Da
